const VendorArea = ({ setView }) => {
    const [isVendorLoggedIn, setIsVendorLoggedIn] = useState(false);
    const [vendorView, setVendorView] = useState('dashboard'); // 'dashboard' or 'create'
    const [vendorProducts, setVendorProducts] = useState(mockVendorProducts);

    const renderContent = () => {
        if (!isVendorLoggedIn) return <VendorLogin setIsVendorLoggedIn={setIsVendorLoggedIn} />;
        switch (vendorView) {
            case 'create': return <CreateProductForm setVendorView={setVendorView} />;
            case 'dashboard':
            default: return <VendorDashboard setVendorView={setVendorView} setVendorProducts={setVendorProducts} />;
        }
    };

    return (<main className="container mx-auto px-4 py-8">{renderContent()}</main>);
};


// --- MAIN APP COMPONENT ---

export default function App() {
  const [view, setView] = useState({ page: 'home', id: null });
  const [searchTerm, setSearchTerm] = useState('');
  const [filteredProducts, setFilteredProducts] = useState(mockProducts);
  
  useEffect(() => {
    const handleSearch = () => {
      if (searchTerm.trim() === '') {
        setFilteredProducts(mockProducts);
      } else {
        const lowercasedQuery = searchTerm.toLowerCase();
        const results = mockProducts.filter(product =>
          product.name.toLowerCase().includes(lowercasedQuery) ||
          product.subtitle.toLowerCase().includes(lowercasedQuery) ||
          product.tags.some(tag => tag.toLowerCase().includes(lowercasedQuery)) ||
          product.vendor.toLowerCase().includes(lowercasedQuery) ||
          product.shortDescription.toLowerCase().includes(lowercasedQuery)
        );
        setFilteredProducts(results);
      }
      if (searchTerm.trim() !== '') {
        setView({ page: 'home', id: null });
      }
    };
    handleSearch();
  }, [searchTerm]);

  const renderView = () => {
    switch (view.page) {
      case 'product':
        const product = mockProducts.find(p => p.id === view.id);
        return product ? <ProductPage product={product} setView={setView} /> : <HomePage products={filteredProducts} setView={setView} />;
      case 'vendor':
        return <VendorArea setView={setView} />;
      case 'home':
      default:
        return <HomePage products={filteredProducts} setView={setView} />;
    }
  };

  return (
    <div className="bg-gray-50 min-h-screen font-sans">
        <Header setView={setView} searchTerm={searchTerm} setSearchTerm={setSearchTerm} />
        {renderView()}
        <Footer />
    </div>
  );
}